"use client";
import React, { useContext } from "react";
import { CaptionsOff, UserPen } from "lucide-react";
import Link from "next/link";
import {
  ISubscripcion,
  SubscriptionStatus,
} from "@/interfaces/Subscripcion.interface";
import { UserContext } from "@/context/user.context";
import { SuscribeContext } from "@/context/Suscribe.context";

const SuscriberProfile: React.FC = () => {
  const { user } = useContext(UserContext);
  const { planes } = useContext(SuscribeContext);
  const sub: ISubscripcion = {
    id: "123e4567-e89b-12d3-a456-426614174000",
    plan: planes[2],
    fechaInicio: new Date("2023-01-01"),
    fechaUltimaPaga: new Date("2023-02-01"),
    fechaVencimiento: new Date("2023-12-31"),
    mercadopagoSubscriptionId: "MP123456789",
    status: SubscriptionStatus.ACTIVE,
    cancellationDate: null,
    cancellationReason: "",
    metadata: {
      specialOffer: "Discount for early renewal",
    },
  };

  return (
    <section className="px-2 py-3 mx-auto max-w-7xl">
      <div className="relative w-[75vw] mx-auto overflow-hidden bg-gray-800 shadow-2xl sm:px-6 rounded-2xl flex flex-col">
        {/* Cabecera con icono, nombre, id y empresa */}
        <div className="relative flex flex-wrap items-center justify-between gap-3 px-4 py-6 md:flex-row">
          <div className="flex items-center gap-3">
            <Link
              href="/"
              className="transition-transform transform hover:scale-110"
            >
              <CaptionsOff size={48} className="text-indigo-200/65" />
            </Link>
            <div className="flex flex-col">
              <h2 className="text-4xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400">
                {sub.plan.name}
              </h2>
            </div>
          </div>
          <p className="w-full text-2xl font-bold text-transparent bg-clip-text bg-gradient-to-r from-indigo-400 via-purple-400 to-pink-400 md:text-right md:w-auto">
            {sub.plan.precio > 0 ? `$${sub.plan.precio}/m` : "Free"}
          </p>
        </div>
        <div className="grid grid-cols-1 gap-6 md:grid-cols-2">
          <div>
            <p className="block text-gray-300">
              <strong>Nombre:</strong> {}
            </p>
          </div>
          <div>
            <p className="block text-gray-300">
              <strong>Email:</strong> {}
            </p>
          </div>
          <div>
            <p className="block text-gray-300">
              <strong>Teléfono:</strong> {}
            </p>
          </div>
          <div>
            <p className="block text-gray-300">
              <strong>Empresa:</strong> {}
            </p>
          </div>
        </div>
        <div className="flex justify-end mt-4">
          <button className="transition-transform transform text-indigo-200/65 hover:scale-110">
            <UserPen size={35} />
          </button>
        </div>
      </div>
    </section>
  );
};

export default SuscriberProfile;
